const axios = require('axios');

var CryptoJS = require("crypto-js");

var encrypt = (inp,pw) =>  CryptoJS.AES.encrypt(inp, pw).toString();
var decrypt = (inp,pw) =>  CryptoJS.AES.decrypt(inp, pw).toString(CryptoJS.enc.Utf8);

export function daysLeftAPI(callback){
    axios.get('/daysLeft')
    .then(function (response) {
      console.log(response);
      callback(response.data.daysleft);
    })
    .catch(function (error) {
      // handle error
      console.log(error);
    })
}

export function textGetAPI(pw,date,callback1,callback2){
    axios.get('/days/'+date)
    .then(function (response) {
      if(Object.values(response.data).length > 0){
        callback1(decrypt(response.data.dataKay,pw));
        callback2(decrypt(response.data.dataChris,pw));
      }else{
        callback1("");
        callback2("");
      }
    })
    .catch(function (error) {
      // handle error
      console.log(error);
    })
}

export function saveTextAPI(pw,text1, text2,date){
    axios.post('/days',{
        date: date,
        dataChris: encrypt(text2,pw),
        dataKay: encrypt(text1,pw)
    })
    .catch(function (error) {
      // handle error
      console.log(error);
    })
}

